#blog {
    width: 800px;
    margin: 0 auto;
}

.blogtitle {
    text-align: center;
    font-size: 25px;
}

.blogwriter {
    text-align: center;
    font-size: 20px;
    color: #000;
}

.blogdate {
    text-align: center;
    font-size: 10px;
    color: #000;
}

#blogtext pre{
    font-family: 'Times New Roman', Times, serif;
    font-size: 20px;
    color: #000;
    white-space: pre-wrap;
    word-wrap: break-word;
}
